#TodoHistoryTodoItem {
  padding: 8px;
  font-size: 14px;
  display: flex;
  justify-content: space-between;
  .text {
    .time {
      color: #999;
      margin-right: 8px;
    }
    .description {
      color: #222;
    }
  }
  .action {
    display: none;
    span{
      color:deepskyblue;
      cursor: pointer;
      &:first-child{
        margin-right: 4px;
      }
    }
  }
  &:hover {
    background: #f4f4f4;
    cursor: default;
    > .action {
      display: block;
    }
  }
}
